<template>
  <div>
    <div class="header">
      <div class="header-left" @click="backHandle">
          <div class="iconfont back-icon">&#xe624;</div>
      </div>
      <h1 class="header-right">{{list.title}}</h1>
    </div>
    <div class="main">
      <router-link :to="'/detail/' + item.id" tag="div" v-for="item of list.children" :key="item.id">
        <div class="item-wrapper-img">
          <img class="item-img" :src="item.imgUrl"/>
        </div>
        <div class="item-info">
          <div class="item-info-box">
            <h4 class="item-title">{{item.title}}</h4>
            <div class="item-price-info">
              <i class="iconfont money">&#xe645;</i>
              <span class="price">{{item.price}}</span>
              <span>起</span>
            </div>
          </div>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
const vm = {
  name: 'weekendTop',
  data () {
    return {
      list: []
    }
  },
  methods: {
    backHandle () {
      this.$router.back()
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      if (to.params.list) {
        console.log(to.params.list)
        vm.list = to.params.list
        const data = {
          'weekendTopList': to.params.list
        }
        try {
          localStorage.setItem('weekendTopList', JSON.stringify(data))
        } catch (e) {}
      } else {
        try {
          vm.list = JSON.parse(localStorage.getItem('weekendTopList')).weekendTopList
        } catch (e) {}
      }
    })
  }
}
export default vm
</script>

<style lang='stylus' scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.header
  display flex
  line-height $headerHeight
  background $bgColor
  color #fff
  .header-left
    width .64rem
    float left
    .back-icon
      text-align center
      font-size .4rem
  .header-right
    flex 1
    text-align center
    margin-right .64rem
.main
  .item-wrapper-img
    overflow hidden
    height 0
    padding-bottom 43.12%
    .item-img
      width 100%
  .item-info
    padding .14rem .2rem .24rem .2rem
    display flex
    flex-flow column nowrap
    .item-info-box
      display flex
    .item-title
      width 66.7%
      color #212121
      font-size .36rem
      line-height .54rem
      ellipsis()
    .item-price-info
      width 33.3%
      height .54rem
      line-height .54rem
      text-align right
      money(.14rem, .36rem)
    .item-desc
      color #616161
      font-size .26rem
      line-height .45rem
      width 100%
      flex-shrink 0
      ellipsis()
</style>



