<template>
  <div>
    <div class="btn-back" @click="handleBack">
      <div class="iconfont btn-back-icon">&#xe624;</div>
    </div>
    <div class="header" :style="styles">
      <div class="header-left" @click="handleBack">
        <div class="iconfont back-icon">&#xe624;</div>
      </div>
      <h1 class="header-right">{{sightName}}</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  props: ['opacity', 'sightName'],
  computed: {
    styles () {
      return {'opacity': this.opacity}
    }
  },
  methods: {
    handleBack () {
      this.$router.back()
    }
  }
}
</script>

<style lang='stylus' scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.btn-back
  z-index 99
  position absolute
  left .1rem
  top .1rem
  width .7rem
  height .7rem
  line-height .7rem
  border-radius .4rem
  text-align center
  background rgba(0, 0, 0, .6)
  .btn-back-icon
    color #fff
    font-size .4rem
.header
  z-index 98
  display flex
  line-height $headerHeight
  background $bgColor
  color #fff
  position fixed
  top 0
  left 0
  right 0
  .header-left
    width .64rem
    float left
    .back-icon
      text-align center
      font-size .4rem
  .header-right
    flex 1
    text-align center
    margin-right .64rem
</style>



