<template>
  <div class="basic-info">
    <div class="card">
      <div class="card-comment border-right">
        <div>
          <span class="card-comment-score">4.8</span>
          <span class="card-comment-text">分</span>
          <span class="card-comment-desc">很棒</span>
        </div>
        <div>
          <span class="card-comment-total">11086条评论</span>
          <span class="card-comment-strategy">14条攻略</span>
          <i class="iconfont info-icon">&#xe735;</i>
        </div>
      </div>
      <div class="card-intro">
        <div>
          <span class="card-intro-title">景点简介</span>
        </div>
        <div>
          <span class="card-intro-text">开放时间、贴士</span>
          <i class="iconfont info-icon">&#xe735;</i>
        </div>
      </div>
    </div>
    <div class="location-card border-top">
      <i class="iconfont location-icon">&#xe611;</i>
      <span class="location-card-title">广东省广州市番禺区外环西路</span>
      <i class="iconfont right-icon">&#xe735;</i>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailCard'
}
</script>

<style lang='stylus' scoped>
.basic-info
  display flex
  flex-flow column
  padding .1rem .2rem 0 .2rem
  background #fff
  .card
    display flex
    .card-comment
      width 50%
      padding .1rem 0 .2rem
      position relative
      .card-comment-score
        font-size .44rem
        line-height .44rem
        color #ff8300
      .card-comment-text
      .card-comment-desc
        font-size .28rem
        line-height .48rem
        color #ff8300
      .card-comment-desc
        margin-left .2rem
      .card-comment-total
      .card-comment-strategy
        font-size .24rem
        color #9e9e9e
        line-height .32rem
        margin-right .2rem
    .card-intro
      width 50%
      padding .1rem 0 .2rem
      position relative
      .card-intro-title
        font-size .28rem
        color #212121
        line-height .48rem
        padding-left .3rem
      .card-intro-text
        color #9e9e9e
        font-size .24rem
        padding-left .3rem
  .info-icon
    position absolute
    top .3rem
    right .2rem
    color #9e9e9e
  .location-card
    margin-top .1rem
    position relative
    padding .2rem 0
    line-height .36rem
    color #212121
    width 100%
    .location-icon
      font-size .3rem
    .location-card-title
      padding-left .2rem
    .right-icon
     float right
     margin-right .2rem
</style>



