<template>
  <div class="detail">
    <detail-header :opacity="opacity" :sightName="sightName"></detail-header>
    <detail-banner :sightName="sightName" :bannerImg="bannerImg" :gallaryImgs="gallaryImgs"></detail-banner>
    <detail-card></detail-card>
    <detail-ticket :list="list"></detail-ticket>
  </div>
</template>

<script>
import DetailHeader from '@/views/detail/Header'
import DetailBanner from '@/views/detail/Banner'
import DetailTicket from '@/views/detail/Ticket'
import DetailCard from '@/views/detail/Card'
import axios from 'axios'
export default {
  name: 'detail',
  data () {
    return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      list: [],
      opacity: 0
    }
  },
  components: {
    DetailBanner,
    DetailHeader,
    DetailTicket,
    DetailCard
  },
  computed: {
    pageY () {
      return window.pageYOffset
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('https://easy-mock.com/mock/5aefe7c7825f6e78c848a81a/travel/detail', {
        params: {
          id: this.$route.params.id
        }
      })
        .then(this.setDetailInfo)
        .catch((e) => {})
    },
    setDetailInfo (res) {
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
        this.gallaryImgs = data.gallaryImgs
        this.list = data.categoryList
      }
    },
    handleScroll () {
      this.opacity = (document.documentElement.scrollTop || window.pageYOffset || document.body.scrollTop) / 180
      if (this.opacity > 1) {
        this.opacity = 1
      }
    }
  },
  mounted () {
    this.getDetailInfo()
    window.addEventListener('scroll', this.handleScroll)
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang='stylus' scoped>
.detail
  background #f5f5f5
</style>



