<template>
  <div class="ticket">
    <div class="ticket-title border-bottom">
      <span @click="handleToTicket">门票</span>
      <span @click="handleToOneDay">一日游</span>
      <div class="underline" :style="{left: left}"></div>
    </div>
    <div class="item" v-for="(item, indexParent) of list" :key="indexParent">
      <div class="item-title border-bottom">
        <span class="item-title-icon"></span>
        {{item.title}}
      </div>
      <div v-for="(item, indexChild) of item.children" :key="indexChild">
        <div class="item-variety border-bottom" @click="showChildren(item)">
          <h5>{{item.title}}</h5>
          <div class="item-variety-info">
            <i class="iconfont money">&#xe645;</i>
            <span class="price">{{item.price}}</span>
            <span class="item-variety-font">起</span>
            <i v-if="item.show" class="iconfont item-variety-arrows">&#xe62d;</i>
            <i v-else class="iconfont item-variety-arrows">&#xe62e;</i>
          </div>
        </div>
        <div v-show="item.show">
          <div v-for="(item, index) of item.children" :key="index">
            <div class="item-agency border-bottom">
              <div class="item-agency-left border-right">
                <h5 class="item-agency-title">{{item.title}}</h5>
                <p class="item-agency-desc">{{item.desc}}</p>
                <p class="item-agency-tip">
                  <span>
                    <i class="iconfont tip-icon">&#xe629;</i>
                    09:00前可订今日&nbsp;&nbsp;
                  </span>
                  <span>
                    <i class="iconfont tip-icon">&#xe601;</i>
                    条件退
                  </span>
                </p>
              </div>
              <div class="item-agency-right">
                <div class="item-agency-money">
                  <i class="iconfont money">&#xe645;</i>
                  <span class="price">{{item.price}}</span>
                </div>
                <div class="item-agency-book">预定</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailTicket',
  props: ['list'],
  data () {
    return {
      left: '16%',
      countParent: -1,
      countChild: -1
    }
  },
  methods: {
    handleToTicket () {
      this.left = '16%'
    },
    handleToOneDay () {
      this.left = '66%'
    },
    showChildren (item) {
      item.show = !item.show
    }
  }
}
</script>

<style lang='stylus' scoped>
@import '~styles/mixins.styl'
.ticket
  margin-top .16rem
  .ticket-title
    display flex
    position relative
    background #fff
    span
      display block
      width 50%
      height .92rem
      line-height .96rem
      text-align center
      font-size .32rem
    .underline
      position absolute
      bottom 0
      height .04rem
      width 1.4rem
      background-color #00bcd4
  .item
    margin-bottom .16rem
    background #fff
    .item-title-icon
      position relative
      left .06rem
      top .06rem
      display inline-block
      width .36rem
      height .36rem
      background url(http://s.qunarzz.com/piao/image/touch/sight/detail.png) 0 -.45rem no-repeat
      margin-right .1rem
      background-size .4rem 3rem
    .item-title
      line-height .8rem
      font-size .32rem
      padding 0 .2rem
    .item-variety
      line-height .8rem
      font-size .32rem
      padding 0 .2rem
      display flex
      justify-content space-between
      h5
        ellipsis()
        max-width 70%
      .item-variety-info
        font-size 0
        money(.14rem, .36rem)
        .item-variety-font
          font-size .14rem
          color #616161
        .item-variety-arrows
          color #bbb
          font-size .24rem
    .item-agency
      background #F5F5F5
      display flex
      justify-content space-between
      padding .25rem
      .item-agency-left
        width 69.33%
        p
          line-height .68rem
          ellipsis()
        .item-agency-desc
          text-indent .1rem
          color #888
        .item-agency-tip
          font-size .16rem
          .tip-icon
            font-size .1rem
      .item-agency-right
        width 30.67%
        display flex
        flex-flow column nowrap
        justify-content center
        align-items center
        align-content center
        money(.14rem, .32rem)
        .item-agency-money
          font-size 0
          width 100%
          text-align center
        .item-agency-book
          margin-left 0.1rem
          border-radius .1rem
          width 1.6rem
          text-align center
          line-height .6rem
          font-weight bold
          background #ff8300
          color #fff
.hide
  display none
</style>



